body {
  background-color: #1d1d1d;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

@font-face {
  font-family: "EurostileBold";
  src: local("EurostileBold"),
    url("./fonts/EurostileBold.ttf") format("truetype");
}

@font-face {
  font-family: "EurostileExtended";
  src: local("EurostileExtended"),
    url("./fonts/EurostileExtended.ttf") format("truetype");
}

@font-face {
  font-family: "EurostileOblique";
  src: local("EurostileOblique"),
    url("./fonts/EurostileOblique.ttf") format("truetype");
}

@font-face {
  font-family: "EuroStyle Normal";
  src: local("EuroStyle Normal"),
    url("./fonts/EuroStyle Normal.ttf") format("truetype");
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
